.menu-block {
    margin: 60px;
    text-align: center;
    
    border-radius: 20px;
    padding: 24px;
    background-color: #3378d8cb;
    color: #fff;

    .menu-title {

        margin-top: 60px;
        margin-bottom: 50px;
    }

    .col-md-6 {
        padding: 34px;
        margin-bottom: 48px;
        text-align: left;
        border-radius: 20px;  
        

        &:hover {
            background-color: rgba(50, 121, 215, 0.94);
            color: #fff;
        }

        img {
            width: 100%;
            margin-bottom: 24px;
            height: 70%;

            &:hover {
                transform: scale(1.03);
                transition: transform 0.3s;
            }
        }
    }

}

.dropdown {
    position: relative;
  
    .dropbtn {
      background-color: #4CAF50;
      color: white;
      padding: 15px;
      font-size: 16px;
      border: none;
      cursor: pointer;
      width: 100%;
      border-radius: 20px;
      font-size: 24px;
      &:focus {
        background-color: #336cbb;
      }
    }
  
    .dropdown-content {
      display: none;
      position: absolute;
      background-color: #fff;
      min-width: 160px;
      box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
      width: 100%;
      border-radius: 20px;
  
      p {
        color: black;
        padding: 12px 16px;
        text-decoration: none;
        display: block;
        
        &:hover {
          background-color: #d6dce6;
        }
      }
    }
  }
